import React from 'react'
import logo from './logo.svg'
import './App.css'
import { useState, createElement } from 'react'
import NotificationMaker from './components/NotificationMaker'
import Init from './components/Init'
import Dashboard from './components/Dashboard'

// find out how to put await inside App() react function
const opfsRoot = await navigator.storage.getDirectory()

// Set up App-wide port for messaging serviceWorker and triggering its notification
const port_to_sw = {}
navigator.serviceWorker.ready.then((registration) => {
  // At this point, you can call methods that require an active
  // service worker, like registration.pushManager.subscribe()
  const page_sw_Channel = new MessageChannel();

  registration.active.postMessage({
      type: 'CONNECT'
  }, [page_sw_Channel.port2]);
  

  page_sw_Channel.port1.onmessage = function(event) {
    console.log('Message received in page on page_sw_Channel.port1:', event.data);
  };

  port_to_sw.postMessage = function(msg) {
    page_sw_Channel.port1.postMessage(msg);
  }
  port_to_sw.showNotification = function({title, options=null}) {
    console.log(`showing notification`)
    registration.showNotification(title, options)
  }
})

// Detect dark mode
if (window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches) {
  const htmlElement = document.documentElement;
  htmlElement.setAttribute('data-bs-theme', "dark")
} else {
}


function App() {
  console.log('render app')
  
  // Prepare a maincontent jsx, which can modify itself
  // `init` is used to give it an initial content
  // if setMaincontent is used unconditionally, will lead to infinite loop of re-rendering
  // cannot use setMaincontent in initial useState declaration as setMaincontent isn't declared until that line executes
  const [maincontent, setMaincontent] = useState(`init`)
  if (maincontent===`init`) {setMaincontent(
    <Init opfsRoot={opfsRoot} setMaincontent={setMaincontent} createElement={createElement} Dashboard={Dashboard}/>
  )}
  
  return (
    <div className="App">
      <header className="App-header">
        <NotificationMaker port_to_sw={port_to_sw} />
      </header>
      <main className="Main">
        {maincontent}
      </main>
      <footer>
      </footer>
    </div>
  );
}

export default App;
