import { useState } from 'react'

export default function PriorityInputRange() {
    console.log('render PriorityInputRange')
    const [priority, setPriority] = useState(5)
    const showPriority = function(event) {
        setPriority(event.target.value)
    }
    return (
        <div class="wrap-range-vertical">
            <div>
                <label htmlFor="Priority_Input" className="form-label">Priority: {priority}</label>
            </div>
            <datalist id="markers">
                <option value="0" label="0-"></option>
                <option value="3" label="3-"></option>
                <option value="6" label="6-"></option>
                <option value="9" label="9-"></option>
            </datalist>
            <input
                type="range"
                id="Priority_Input"
                name="Priority_Input"
                min="0"
                max="9"
                onChange={showPriority}
                value={priority}
                list="markers"
            />

        </div>
    )
}